import React, { useEffect, useState } from 'react'
import { firestore } from './firebase'
import './Feed.css'
import Useronline from './Useronline'
import Handshake from './Handshake'

const Feed = () => {
    const[target,setTarget]=useState(null)
    const [pp, setPp] = useState(null)
    const[name,setName]=useState(null)
    const func = (target)=>{
        setTarget(target)
        console.log(target)
        const n =target.indexOf('imageof')
        const m =target.indexOf('?')
        const slice = target.slice(parseInt(n+7),parseInt(m))
        setName(slice)
    }

    useEffect(()=>{firestore.collection('profilepicture').onSnapshot(
        snap => {
            setPp(snap.docs.map(doc => (
                doc.data()
            )))
        }
    )}, [])


    return (
        <>
        {target&&<div className='target'>
            
            <div className='targetbox'>
           <div><img className='targetimg' alt='target' src={target}/></div>
             <div className='nameimg'>
                  
                 </div>
             <Handshake name={name}/>
            </div>
        
            </div>}
        <div className='feed'>
            {pp && pp.map(user => (<Useronline key={user.user} url={user.url} name={user.user} func={func}/>))}
        </div>
        </>
    )
}

export default Feed
