import React, { useState } from 'react'
import './Home.css'
import { Link } from 'react-router-dom'
import Logo from './logo.png'
import { auth } from './firebase'
import './Error.css'

const SIgnup = () => {

    const [user, setUser] = useState('')
    const [pass, setPass] = useState('')
    const [email, setEmail] = useState('')
    const [confirmpass, setConfirmpass] = useState('')
    const [err, setErr] = useState(null)
    const [done, setDone] = useState(true)
    const [message, setMessage] = useState(null)
    const[msg,setMsg]= useState(null)

    const handleUser = (e) => {
        e.preventDefault()
        setUser(e.target.value)
    }
    const handlePass = (e) => {
        e.preventDefault()
        setPass(e.target.value)
    }
    const handleCpass=(e)=>{
        e.preventDefault()
        setConfirmpass(e.target.value)
    }
    const handleEmail = (e) => {
        e.preventDefault()
        setEmail(e.target.value)
    }

    const signup = () => {
        if (parseInt((pass.length)) < 6 || (!email.includes('@')) || (!(pass === confirmpass))) {
            setErr('email address must be valid & password should be at lease 6 characters & your password and confirm password should be the same')
        }
        else {
            auth.createUserWithEmailAndPassword(email, pass).catch(err=>{
               return (setErr(err.message))
            }).then(
                kred => {
                    if(kred){
                    setDone(false)
                    setMessage('welcome' +user+"Thanks for siging up in Benny's universe now you can sign in and enjoy! ")
                    console.log(kred.user)}
                    else{
                        setMsg('eror ')
                    }

                }
            )
        }
    }



    return (
        <div>
            {err && <div className='err'>
                {err}     
            </div>}
            {msg && <div className='message'>
                    {msg}
                    </div>}
            {message && <div className='msg'>
                {message}
            </div>}
            

            {done && <div className='rgForm'>
                <div className='robo'>
                    <img src={Logo} alt='logo' className='robot' />
                </div>
                <div className='form'>
                    <label className='label'>
                        <p> username:
                   </p>
                        <input className='input' onChange={handleUser} />
                    </label>
                </div>
                <div className='form'>
                    <label className='label'>
                        <p>email:</p>
                        <input className='input' onChange={handleEmail} />
                    </label>
                </div>
                <div className='form'>
                    <label className='label'>

                        <p>password:</p>
                        <input className='input' type='password' onChange={handlePass} />
                    </label>
                </div>
                <div className='form'>
                    <label className='label'>

                        <p>confirm password:</p>
                        <input className='input' type='password' onChange={handleCpass} />
                    </label>
                </div>
                <div className='button'>
                    <button className='btn' onClick={signup}>
                        Sign up
                </button>
                </div>
            </div>}
        </div>
    )
}

export default SIgnup
