import React,{useContext} from 'react'
import './Feed.css'
import { Avatar } from '@material-ui/core'
import {Context} from  './App'


const Useronline = ({ url, name,func }) => {

    const{lastseen,creation,user}=useContext(Context)

    const handle = (e)=>{
        func(e.target.src)
    }

    return (
        <div className='useronline'>
            <div className='container'>
                <div className='ava'>
                    <Avatar className='avauser' onClick={handle} src={url} />
                </div>
                <div className='name'>
                    {name}
                </div>
                <div className='box'>
                   { (name===user)?<h3>lastime login @<br/><span className='txt'> 
                    {lastseen}</span></h3>:null}
                </div>
                <div className='status'>
                        {(name===user)?<p className='online'>online</p>:<p className='offline'>offline</p>}
                </div>
            </div>
        </div>
    )
}

export default Useronline

