import React,{useState} from 'react'
import './Form.css'
import Logo from './logo.png'
import {Link} from 'react-router-dom'
import {auth, storage,firestore} from './firebase'
import CloudUploadIcon from '@material-ui/icons/CloudUpload'
import {Avatar} from '@material-ui/core'


const Signin = ({func}) => {

    const [user, setUser] = useState('')
    const [pass, setPass] = useState('')
    const [email, setEmail] = useState('')
    const[done,setDone]=useState(true)
    const[err,setErr]=useState(null)
    const[msg,setMsg] = useState(null)
    const[home,setHome]=useState(null)

    const handleUpload=(e)=>{
        e.preventDefault()
        const selected = e.target.files[0]
        const ref = storage.ref(`profilepicture`).child('imageof'+user)
        ref.put(selected)
        .on('state_changed', snap=>{
            const progress = (snap.bytesTransferred / snap.totalBytes) * 100
            console.log('Upload is ' + progress + '% done')
            setMsg('Upload is ' + progress + '% done')
            if(progress == '100'){
                setMsg('thanks for uploading your profile picture now you can go home!')
                setHome(true)
            }
        },err=>{console.log(err)},async ()=>{
            const url = await ref.getDownloadURL()
            firestore.collection(`profilepicture`).add({user,url})
        })
    }

    const handleUser = (e) => {
        e.preventDefault()
        setUser(e.target.value)
    }
    const handlePass = (e) => {
        e.preventDefault()
        setPass(e.target.value)
    }
    const handleEmail = (e) => {
        e.preventDefault()
        setEmail(e.target.value)
    }

    const login=()=>{
        console.log(user,pass,email)
        setDone(false)
        auth.signInWithEmailAndPassword(email,pass).then(
            kred =>{
                 console.log(kred.user.metadata)
                 func(kred.user.metadata.creationTime
                 ,kred.user.metadata.lastSignInTime,user)
                 
            }
        ).catch(err =>{
                setErr(err.message)
        })
    }

    return (
        <>
        
            {err && <div className='err'>
                {err}
            </div>}
        {done && <div className='rgForm'>
            <div className='robo'>
            <img src={Logo} alt='logo' className='robot'/>
            </div>
            <div className='form'>
                <label className='label'>
                    <p> username:
                   </p>
                    <input className='input' onChange={handleUser}/>
                </label>
            </div>
            <div className='form'>
                <label className='label'>
                    <p>email:</p>
                    <input className='input' onChange={handleEmail} />
                </label>
            </div>
            <div className='form'>
                <label className='label'>

                    <p>password:</p>
                    <input className='input' type='password' onChange={handlePass} />
                </label>
            </div>
            <div className='button'>
               <button className='btn' onClick={login}>
                   login
                </button> 
            </div>
        </div>}
        {(!done) && (!err) && (!home) && <div className='main'>
            <div className='welcome'>
                welcome to Benny's Universe! 
            </div>
            <div className='profile'>

                <label>
                    <CloudUploadIcon className='uploadicon'/>
                    choose a photo to upload your profile picture or visit home
                    <input type='file' className='proinput' onChange={handleUpload}/>
                </label>
                   
            </div>
            <div className='homelink'>
                <Link to='/home'>home</Link>
            </div>
            <div className='progress'>
              {msg}
            </div>
            <div className='avatar'>
                <div className='f'>
                <Avatar className='ava' src=''/>
                {user}
                </div>
                <p className='level'>First task</p>
             </div>

        </div>
    
        }
        {home && <div className='homelink'><Link to='/home'>Home</Link></div>}
        </>
    )
}

export default Signin
